
.container{
  text-align: center;
  margin:0 auto;
  align-content: center;

}
.upbar{
  padding-top: 10px;
  height:40px;
  background-color: rgb(210,157,112);
  color: white;
  font-size: 13pt;
}
.normalcard{
  border-radius: 10px;
  width:90px;
  height:100px;
  box-shadow: 0px 0px 5px gray;
  background-color: rgb(255, 255, 255);
  -webkit-transition: -webkit-box-shadow 0.2s,-webkit-transform 0.2s;
  transition: box-shadow 0.2s,transform 0.2s
}
.page{
  width:90px;
  height:100px;
  box-shadow: 0px 0px 8px rgb(172, 117, 69);
  background-color: rgb(255, 255, 255);
  -webkit-transition: -webkit-transform 0.2s;
  transition: transform 2s;
  transform: scale(0.90,0.90);

}


.selectedcard{
  width:90px;
  height:100px;
  border-radius: 10px;
 
  box-shadow: 0px 0px 8px rgb(210,157,112);
  background-color: rgb(255, 255, 255);
  -webkit-transition: -webkit-box-shadow 0.2s, -webkit-transform 0.2s;
  transition: box-shadow 0.2s, transform 0.2s;
  transform: scale(0.90,0.90);
}

.cardname{
  border-radius: 0px 0px 10px 10px;
  background-color:rgb(210, 158, 112);
  height:25px;
  margin-top: -4px;
  color:white;
  font-size:10pt;
}


.step{
  margin-top:18px;
  background: rgb(210,157,112);
  color:white;
  height:28px;
  width:58px;
  align-content: center;
  font-size:14pt;

}
.step_question{
  margin-left:10px;
  color: rgb(210,157,112);
}
.name{
  background: rgb(210,157,112);
  color:white;
  font-size:12pt;
}




.btn{
  background: rgba(128, 255, 0, 0);
  margin-top: 10px;
  border:rgba(128, 255, 0, 0)
}
.logo{
  border-radius: 50px;
  width:200pt;
  height:200pt;
}
.nextbtn{
  box-shadow: 0px 2px 5px rgb(146, 120, 120);
  margin: 0 auto;
  background-color: rgb(210,157,112);
  color: white;
  width: 400px;
  height: 60px;
  border-radius: 100px 100px 100px 100px;
  -webkit-transition:  -webkit-transform 0.2s;
  transition:  transform 0.2s; 
}
.nextbtn:active{
  transform: scale(0.95,0.95);
  -webkit-transition:  -webkit-transform 0.2s;
  transition:  transform 0.2s; 
}
.qbox{
  box-shadow: 0px 2px 5px rgb(145, 145, 145);
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  color: rgb(210,157,112);
  width: 400px;
  height: 60px;
  border-radius: 100px 100px 100px 100px;
  -webkit-transition:  -webkit-transform 0.2s, -webkit-background-color 0.2s;;
  transition:  transform 0.2s,background-color 0.2s; 
}
.selectedqbox{
  box-shadow: 0px 2px 5px rgb(146, 146, 146);
  margin: 0 auto;
  background-color: rgb(243, 243, 243);
  color: rgb(210,157,112);
  width: 400px;
  height: 60px;
  border-radius: 100px 100px 100px 100px;
  transform: scale(0.9,0.9);
  -webkit-transition:  -webkit-transform 0.2s, -webkit-background-color 0.2s;
  transition:  transform 0.2s, background-color 0.2s; 

}
.textinbox{
  margin-top:10px;
  font-size:15pt;
  padding-top: 12pt;

}
.box{
  margin: 0 auto;
  width:300pt;
}
.grid{
  display:grid;
  grid-template-columns:100px 100px 100px 100px;
  column-gap: 3px;
  row-gap:10px;
}

.rankbox{
  padding-top:10px;
  padding-bottom: 10px;
  width:360px;
  margin: 0 auto;
  border-style: solid ;
  border-width: 0.5px;
  border-color: white;
