}
.borderimage{
  width: 180px;
  height: 180px;
  object-fit: contain;
  background-color: rgb(255, 255, 255);
  margin-top:15px;
  border-radius: 100px;
  box-shadow: 0px 0px 10px gray;
}
.btnhoverrange{
  border-radius: 100px;
  background-color: rgb(0, 0, 0);
  height:180px;
  width:180px;
  margin-top:15px;
  opacity: 0;
}
.rank{
  -webkit-transition: -webkit-opacity 0.2s;
  transition: opacity 0.2s; 
  font-size:auto;
  width:180px;
  height:107px;
  border-radius: 0px 0px 100px 100px;
  margin-top:88px;
  opacity: 0;
  color:rgb(255, 255, 255);
  background-image:  linear-gradient( to top, rgba(0, 0, 0, 0.76)20%, rgba(0, 0, 0, 0) );
}
.hoverrank{
  -webkit-transition:  -webkit-opacity 0.2s;
  opacity: 1;
  font-size:auto;
  width:180px;
  height:107px;
  border-radius: 0px 0px 100px 100px;
  margin-top:88px;
  
  color:rgb(255, 255, 255);
  background-image:  linear-gradient( to top, rgba(0, 0, 0, 0.76)20%, rgba(0, 0, 0, 0) );
  transition: opacity 0.2s; 
}
